<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">영수증 신청내역</h2>
      <p class="content-sub-title">월별 영수증 신청 내역 확인 페이지</p>

      <div class="d-flex justify-content-end align-items-center mb-3">
        <label for="monthSearch" class="mr-2">월 선택 :</label>
        <select class="form-control w-auto" id="monthSearch" v-model="selectedMonth" @change="filterByMonth">
          <option value="01">1월</option>
          <option value="02">2월</option>
          <option value="03">3월</option>
          <option value="04">4월</option>
          <option value="05">5월</option>
          <option value="06">6월</option>
          <option value="07">7월</option>
          <option value="08">8월</option>
          <option value="09">9월</option>
          <option value="10">10월</option>
          <option value="11">11월</option>
          <option value="12">12월</option>
        </select>
      </div>

      <!-- 테이블 보기 (큰 화면에서만 보임) -->
      <table class="table mt-3" id="dataTable" v-if="!isMobile">
        <thead>
          <tr>
            <th>발생일</th>
            <th>구분</th>
            <th>사유</th>
            <th>금액</th>
            <th>영수증 사진</th>
            <th>결과</th>
            <th>반려사유</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in paginatedData" :key="index">
            <td>{{ item.date }}</td>
            <td>{{ item.type }}</td>
            <td>{{ item.reason }}</td>
            <td>{{ item.amount.toLocaleString() }}원</td>
            <td><a href="#" @click.prevent="previewReceipt(item.receipt)" class="text-primary">{{ item.receiptName }}</a></td>
            <!-- <td><img :src="item.receipt" alt="영수증 이미지" width="50" @click="previewReceipt(item.receipt)" style="cursor: pointer;" /></td> -->
            <td :class="getResultClass(item.result)">{{ item.result }}</td>
            <td>{{ item.rejectReason }}</td>
          </tr>
        </tbody>
      </table>

      <!-- 카드 레이아웃 보기 (작은 화면에서만 보임) -->
      <div class="card-layout" v-if="isMobile">
        <div class="card" v-for="(item, index) in paginatedData" :key="index">
          <div class="card-header">
            <p class="card-title">{{ item.date }}</p>
          </div>
          <div class="card-body">
            <p class="card-text">
              <strong>구분:</strong> {{ item.type }} | 
              <strong>금액:</strong> {{ item.amount.toLocaleString() }}원
            </p>
            <p class="card-text"><strong>사유:</strong> {{ item.reason }}</p>
            <p class="card-text">
              <strong>영수증 사진: </strong> 
              <a href="#" @click.prevent="previewReceipt(item.receipt)" class="text-primary">{{ item.receiptName }}</a>
            </p>
            <p class="card-text">
              <strong>결과:</strong> <span :class="getResultClass(item.result)">{{ item.result }}</span> | 
              <strong>반려사유:</strong> {{ item.rejectReason }}
            </p>
          </div>
        </div>
      </div>

      <!-- 페이징 -->
      <div class="d-flex justify-content-center">
        <ul class="pagination">
          <li class="page-item" :class="{ disabled: currentPage === 1 }" @click="prevPage"><a class="page-link" href="#">&lt;</a></li>
          <li class="page-item"><a class="page-link" href="#">{{ currentPage }}</a></li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }" @click="nextPage"><a class="page-link" href="#">&gt;</a></li>
        </ul>
      </div>
    </div>

    <!-- 미리보기 모달 -->
    <div class="modal fade" id="previewModal" tabindex="-1" role="dialog" aria-labelledby="previewModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="previewModalLabel">영수증 미리보기</h5>
            <button type="button" class="close ms-auto" data-bs-dismiss="modal" aria-label="Close" style="font-size: 1.5rem; font-weight: bold;">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body" @click="closePreviewModal">
            <img :src="selectedReceipt" alt="영수증 미리보기" class="img-fluid" />
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import * as bootstrap from 'bootstrap'; // Bootstrap 모듈 가져오기

const selectedMonth = ref('01');
const currentPage = ref(1);
const rowsPerPage = ref(5);
const data = ref([
  { date: '2024-01-01', type: '식비', reason: '점심 식사', amount: 10000, receiptName: '영수증 사진 이름-1', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', result: '신청중', rejectReason: '' },
  { date: '2024-01-02', type: '교통비', reason: '출장 교통', amount: 15000, receiptName: '영수증 사진 이름-2', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', result: '반려', rejectReason: '영수증 불명확' },
  { date: '2024-02-01', type: '사무용품', reason: '프린터 잉크 구매', amount: 20000, receiptName: '영수증 사진 이름-3', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', result: '승인', rejectReason: '' },
  // 추가 데이터...
]);

const isMobile = ref(window.innerWidth <= 1150);
const selectedReceipt = ref(null);

const filteredData = computed(() => {
  return data.value.filter(item => item.date.startsWith(`2024-${selectedMonth.value}`));
});

const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * rowsPerPage.value;
  return filteredData.value.slice(start, start + rowsPerPage.value);
});

const totalPages = computed(() => {
  return Math.ceil(filteredData.value.length / rowsPerPage.value);
});

const filterByMonth = () => {
  currentPage.value = 1;
};

const prevPage = () => {
  if (currentPage.value > 1) {
    currentPage.value--;
  }
};

const nextPage = () => {
  if (currentPage.value < totalPages.value) {
    currentPage.value++;
  }
};

const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 1150;
};

const previewReceipt = (receiptUrl) => {
  selectedReceipt.value = receiptUrl;
  const modal = new bootstrap.Modal(document.getElementById('previewModal'));
  modal.show();
};

const closePreviewModal = () => {
  const modal = bootstrap.Modal.getInstance(document.getElementById('previewModal'));
  modal.hide();
};

const getResultClass = (result) => {
  if (result === '신청중') return 'status-pending';
  if (result === '승인') return 'status-approved';
  if (result === '반려') return 'status-rejected';
  return '';
};

onMounted(() => {
  window.addEventListener('resize', updateIsMobile);
});
</script>

