<template>
  <div>
    <!-- 모바일 토글 버튼 -->
    <button v-if="isMobile" class="mobile-menu-toggle" @click="toggleSidebar" :class="{ 'top-zero': isSidebarOpen }">
      <span v-if="isSidebarOpen" class="close-icon">&times;</span>
      <span v-else class="menu-icon">&#9776;</span>
    </button>

    <!-- 사이드바 -->
    <div class="sidebar" :class="{ 'mobile-open': isSidebarOpen }">
      <!-- 공지사항 메뉴 -->
      <div class="menu-item">
        <router-link to="/com/notice-integrated" @click="closeSidebar" class="main-menu">
          공지사항
        </router-link>
      </div>


      <!-- 영수증 관리 메뉴 -->
      <div class="menu-item">
        <a href="#" @click.prevent="toggleMenu('receipt')" class="main-menu">
          <span class="arrow">{{ menus.receipt ? '─' : '▶' }}</span>
          <span>영수증 관리</span>
        </a>
        <div v-if="menus.receipt" class="submenu">
          <!-- 영수증 관리 어플리케이션 ID : 7000  -->
          
          <router-link to="/receipt/receipt-submission" @click="handleMenuClick('receipt')" class="submenu-item">영수증 등록 (개인)</router-link>
          <router-link to="/receipt/personal-receipt-history" @click="handleMenuClick('receipt')" class="submenu-item">영수증 신청내역(개인)</router-link>
          <router-link to="/receipt/annual-receipt-summary" @click="handleMenuClick('receipt')" class="submenu-item">년도별 영수증 요약(개인)</router-link>

          <router-link to="/receipt/receipt-request-overview" @click="handleMenuClick('receipt')" class="submenu-item">영수증 신청 내역 현황(결재)</router-link>
          <router-link to="/receipt/receipt-personal-request-details" @click="handleMenuClick('receipt')" class="submenu-item">영수증 신청 내역 상세페이지(결재)</router-link>
          <router-link to="/receipt/receipt-detailed-report" @click="handleMenuClick('receipt')" class="submenu-item">영수증 상세 보고서(결재)</router-link>
          <router-link to="/com/user/" @click="handleMenuClick('user')" class="submenu-item">결제 마감 확인(추후)</router-link>
          
          <router-link to="/receipt/receipt-closure" @click="handleMenuClick('receipt')" class="submenu-item">월 마감(마감자)</router-link>
          
        </div>
      </div>

      <!-- 개인정보 관리 메뉴 -->
      <div class="menu-item">
        <a href="#" @click.prevent="toggleMenu('user')" class="main-menu">
          <span class="arrow">{{ menus.user ? '─' : '▶' }}</span>
          <span>개인정보 관리</span>
        </a>
        <div v-if="menus.user" class="submenu">
          <!-- 영수증 관리 어플리케이션 ID : 9000  -->
          <router-link to="/com/user-information" @click="handleMenuClick('user')" class="submenu-item">사용자 정보(공통)</router-link>
          <router-link to="/com/performance-management" @click="handleMenuClick('user')" class="submenu-item">실적 현황(개인)</router-link>
          <router-link to="/com/" @click="handleMenuClick('user')" class="submenu-item">실적 관리(관리자)</router-link>
          <router-link to="/com/" @click="handleMenuClick('user')" class="submenu-item">권한 부여(추후)</router-link>
          <router-link to="/com/" @click="handleMenuClick('user')" class="submenu-item">계좌 관리(추후)</router-link>
        </div>
      </div>

      <!-- 추후 추가 예상 메뉴 -->
      <div class="menu-item">
        <a href="#" @click.prevent="toggleMenu('organization')" class="main-menu">
          <span class="arrow">{{ menus.organization ? '─' : '▶' }}</span>
          <span>추가 예상</span>
        </a>
        <div v-if="menus.organization" class="submenu">
          <router-link to="/org/" @click="handleMenuClick('organization')" class="submenu-item">조직도(추후)</router-link>
          <router-link to="/org/" @click="handleMenuClick('organization')" class="submenu-item">주소록(추후)</router-link>
          <router-link to="/org/" @click="handleMenuClick('organization')" class="submenu-item">메신저(추후)</router-link>
          <router-link to="/org/" @click="handleMenuClick('organization')" class="submenu-item">결제(추후)</router-link>
        </div>
      </div>


      <div class="menu-item">
        <router-link to="/#" @click="closeSidebar" class="main-menu" style="font-size:  0.9rem;">
          사용자 로그인 테스트
        </router-link>
        <router-link to="/admin/login" @click="closeSidebar" class="main-menu" style="font-size:  0.9rem;">
          관리자 로그인 테스트
        </router-link>
      </div>


    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';

// 메뉴 상태 관리
const menus = ref({
  receipt: false, // 영수증 관리 하위 메뉴 표시 여부
  user: false,    // 개인정보 관리 하위 메뉴 표시 여부
  organization: false,    // 조직 관리 하위 메뉴 표시 여부
});

// 모바일 사이드바 열림 상태
const isMobile = ref(window.innerWidth <= 768);
const isSidebarOpen = ref(false);

// 모바일 여부를 업데이트하는 함수
const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 768;
};

// 사이드바 토글 함수
const toggleSidebar = () => {
  isSidebarOpen.value = !isSidebarOpen.value;

  // 사이드바가 열리면 버튼의 위치를 위로 이동
  if (isSidebarOpen.value) {
    document.querySelector('.mobile-menu-toggle').style.top = '85px';
  } else {
    document.querySelector('.mobile-menu-toggle').style.top = '90px';
  }
};

// 메뉴 클릭 후 사이드바 닫기 함수
const closeSidebar = () => {
  isSidebarOpen.value = false;
  const menuToggleButton = document.querySelector('.mobile-menu-toggle');
  if (menuToggleButton) {
    menuToggleButton.style.top = '90px';
  }
};

// 메뉴 토글 함수
const toggleMenu = (menu) => {
  Object.keys(menus.value).forEach(key => {
    if (key !== menu) {
      menus.value[key] = false;
    }
  });

  menus.value[menu] = !menus.value[menu];
};

// 메뉴 클릭 시 호출되는 함수
const handleMenuClick = () => {
  if (isMobile.value) {
    closeSidebar(); // 모바일일 경우 메뉴를 닫음
  }
};

// 모바일 여부 업데이트 이벤트 리스너 등록
window.addEventListener('resize', updateIsMobile);
</script>

<style scoped>
.sidebar {
  background-color: #375a7f;
  height: 100vh;
  padding-top: 30px;
  position: fixed;
  top: 65px;
  left: 0;
  width: 250px;
  transition: transform 0.3s ease;
  z-index: 999;
}

.sidebar.mobile-open {
  transform: translateX(0);
}

.menu-item {
  padding-left: 15px; /* 상위 메뉴 들여쓰기 */
}

.main-menu {
  color: #fff;
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 10px 0;
}

.submenu {
  padding-left: 12px; /* 서브메뉴의 들여쓰기 */
  font-size: 0.8rem;
}

.submenu-item {
  color: #fff;
  text-decoration: none;
  display: block;
  padding: 5px 0 5px 10px; /* 서브메뉴 아이템 패딩 */
}

.main-menu:hover,
.submenu-item:hover {
  background-color: #4f739b; /* 상위 및 하위 메뉴에 마우스 호버 시 색상 변경 */
  color: #fff; /* 호버 시 텍스트 색상 유지 */
}

.arrow {
  margin-right: 5px;
  font-size: 0.5rem;
  font-weight: bold;
}

@media (max-width: 768px) {
  .mobile-menu-toggle {
    top: 90px;
    font-size: 1.5rem; /* 크기 줄임 */
    margin-right: 10px;
    padding: 0 8px; /* 패딩 줄임 */
    border: none;
    background-color: white; /* 배경색을 흰색으로 변경 */
    border: 1px solid #ddd; /* 회색 테두리 추가 */
    border-radius: 3px; /* 모서리를 조금 덜 둥글게 */
    color: black;
    position: fixed;
    right: 10px;
    z-index: 1000;
    cursor: pointer;
    opacity: 0.5; /* 반투명 효과 */
  }

  .mobile-menu-toggle.top-zero {
    background-color: transparent; /* 닫기 토글일 때 배경색 제거 */
    border: none; /* 닫기 토글일 때 테두리 제거 */
    opacity: 1; /* 반투명 효과 제거 */
    margin-right: 10px;
  }

  .mobile-menu-toggle .close-icon {
    color: white;
    font-size: 2rem;
  }

  .mobile-menu-toggle .menu-icon {
    color: black;
  }

  .sidebar {
    transform: translateX(-100%);
    width: 100%;
    height: 100vh;
    top: 65px;
    position: fixed;
    z-index: 999;
  }

  .sidebar.mobile-open {
    transform: translateX(0);
  }

  .sidebar a {
    padding: 10px 5px;
    text-align: left; /* 메뉴를 왼쪽 정렬 */
    border-right: none;
    font-size: 0.75rem;
  }

  .submenu a {
    padding-left: 20px; /* 서브메뉴의 들여쓰기 */
    font-size: 0.7rem;
  }

  .content {
    margin-left: 0;
    padding: 20px;
    font-size: 0.875rem;
  }

  .content-title {
    font-size: 1.25rem;
    margin-bottom: 30px;
  }
}
</style>
