<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">영수증 신청내역 현황</h2>
      <!-- 검색된 날짜 범위 표시 -->
      <div class="search-date-range mt-3 search-month-day" v-if="filteredData.length">
        <p>날짜: [{{ filteredStartDate }} ~ {{ filteredEndDate }}]</p>
      </div>

      <div class="search-controls">
        <!-- 시작일 그룹 -->
        <div class="d-flex align-items-center end-date-group mt-2">
          <label for="startDate" class="mr-2">시작일 :</label>
          <input type="month" class="form-control w-auto" id="startDate" v-model="startDate">
        </div>
        <!-- 종료일 그룹 -->
        <div class="d-flex align-items-center end-date-group mt-2">
          <label for="endDate" class="mr-2">종료일 :</label>
          <input type="month" class="form-control w-auto" id="endDate" v-model="endDate">
        </div>
        <!-- 이름 검색 그룹 -->
        <div class="d-flex align-items-center name-group mt-2">
          <label for="nameSearch" class="mr-2">이름 :</label>
          <input type="text" class="form-control w-auto" id="nameSearch" v-model="nameSearch" placeholder="이름 입력">
          <button class="btn btn-primary ml-2" @click="search">조회</button>
        </div>
      </div>

      <!-- 테이블 보기 -->
      <table class="table mt-3" id="dataTable" v-if="viewMode === 'table'">
        <thead>
          <tr>
            <th>이름</th>
            <th>메일</th>
            <th>건수</th>
            <th>신청금액</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in paginatedData" :key="index">
            <td>{{ item.name }}</td>
            <td>{{ item.email }}</td>
            <td>{{ item.count }}</td>
            <td>{{ item.requested.toLocaleString() }}원</td>
          </tr>
        </tbody>
      </table>

      <!-- 카드 형식 보기 -->
      <div class="card mb-3" v-for="(item, index) in paginatedData" :key="index">
        <div class="card-header">
          <div class="d-flex justify-content-between align-items-center mb-2">
            <p class="card-text m-0">{{ item.name }}</p>
            <p class="card-text m-0 text-right">{{ item.email }}</p>
          </div>
        </div>
        <div class="card-body">
          <p class="card-text">건수: {{ item.count }}</p>
          <p class="card-text">신청금액: {{ item.requested.toLocaleString() }}원</p>
        </div>
      </div>


      <!-- 페이징 컨트롤 -->
      <nav class="d-flex justify-content-center">
        <ul class="pagination">
          <li class="page-item" :class="{ disabled: currentPage === 1 }" @click.prevent="prevPage"><a class="page-link" href="#">&lt;</a></li>
          <li class="page-item"><a class="page-link" href="#">{{ currentPage }}</a></li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }" @click.prevent="nextPage"><a class="page-link" href="#">&gt;</a></li>
        </ul>
      </nav>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
// import axios from 'axios';

const startDate = ref('');
const endDate = ref('');
const nameSearch = ref('');
const currentPage = ref(1);
const rowsPerPage = ref(5);
const viewMode = ref('table'); // 'table' or 'card'
const data = ref([]);
const filteredData = ref([]);

// 예시 데이터 설정
const fetchData = () => {
  data.value = [
    { month: '2024-01', name: '홍길동', email: 'hong@example.com', count: 3, requested: 1000000 },
    { month: '2024-02', name: '김철수', email: 'kim@example.com', count: 5, requested: 1200000 },
    { month: '2024-03', name: '이영희', email: 'lee@example.com', count: 2, requested: 800000 },
    // 추가 데이터...
  ];
  filteredData.value = data.value;
};

const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * rowsPerPage.value;
  return filteredData.value.slice(start, start + rowsPerPage.value);
});

const totalPages = computed(() => {
  return Math.ceil(filteredData.value.length / rowsPerPage.value);
});

const search = () => {
  const searchName = nameSearch.value.toLowerCase();
  filteredData.value = data.value.filter(item => item.name.toLowerCase().includes(searchName));
  currentPage.value = 1;
};

const prevPage = () => {
  if (currentPage.value > 1) {
    currentPage.value--;
  }
};

const nextPage = () => {
  if (currentPage.value < totalPages.value) {
    currentPage.value++;
  }
};

const updateViewMode = () => {
  if (window.innerWidth <= 1150) {
    viewMode.value = 'card';
  } else {
    viewMode.value = 'table';
  }
};

// 검색된 데이터의 시작일과 종료일 계산
const filteredStartDate = computed(() => {
  if (filteredData.value.length === 0) return '';
  return filteredData.value[0].month;
});

const filteredEndDate = computed(() => {
  if (filteredData.value.length === 0) return '';
  return filteredData.value[filteredData.value.length - 1].month;
});

onMounted(() => {
  fetchData();
  updateViewMode();
  window.addEventListener('resize', updateViewMode);
});
</script>

