<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">권범준님 영수증 신청내역</h2>
      <p class="content-sub-title">개인 영수증 신청 내역 확인 페이지</p>
      <!-- 뒤로가기 버튼 추가 -->
      <button class="btn btn-secondary back-button" @click="goBack">뒤로가기</button>
      
      <div class="d-flex align-items-center">
        <span class="mr-2 all-click">전체 승인</span>
        <input type="radio" name="approveAll" id="approveAll" @change="toggleApproveAll" v-model="selectAllDecision" value="approve" class="mr-4" />
        <span class="mr-2 all-click">전체 반려</span>
        <input type="radio" name="rejectAll" id="rejectAll" @change="toggleRejectAll" v-model="selectAllDecision" value="reject" />
      </div>
      <hr>
      <!-- 테이블 보기 (큰 화면에서만 보임) -->
      <table class="table mt-3" id="dataTable" v-if="!isMobile">
        <thead>
          <tr>
            <th>발생일</th>
            <th>구분</th>
            <th>사유</th>
            <th>금액</th>
            <th>영수증 사진</th>
            <th>승인</th>
            <th>반려</th>
            <th>반려사유</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in paginatedData" :key="index">
            <td>{{ item.date }}</td>
            <td>{{ item.type }}</td>
            <td>{{ item.reason }}</td>
            <td>{{ item.amount.toLocaleString() }}원</td>
            <td>
              <a href="#" @click.prevent="previewReceipt(item.receipt)" class="text-primary">{{ item.receiptName }}</a>
            </td>
            <td><input type="radio" :name="'decision' + index" class="approveRadio" v-model="item.decision" value="approve" @change="updateDecision()" /></td>
            <td><input type="radio" :name="'decision' + index" class="rejectRadio" v-model="item.decision" value="reject" @change="updateDecision()" /></td>
            <td><input type="text" class="form-control rejectReason" v-model="item.rejectReason" /></td>
          </tr>
        </tbody>
      </table>

      <!-- 카드 레이아웃 보기 (작은 화면에서만 보임) -->
      <div class="card-layout" v-if="isMobile">
        <div class="card mb-3" v-for="(item, index) in paginatedData" :key="index">
          <div class="card-header">
            <p class="card-title">{{ item.date }}</p>
          </div>
          <div class="card-body">
            <p class="card-text"><strong>구분:</strong> {{ item.type }}<br></p>
            <p class="card-text"><strong>금액:</strong> {{ item.amount.toLocaleString() }}원<br></p>
            <p class="card-text"><strong>사유:</strong> {{ item.reason }}<br></p>
            <p class="card-text">
              <strong>영수증 사진: </strong> 
              <a href="#" @click.prevent="previewReceipt(item.receipt)" class="text-primary">{{ item.receiptName }}</a>
            </p>
            <p class="card-text">
              <strong>결정: </strong>
              <input type="radio" :name="'decision' + index" class="approveRadio" v-model="item.decision" value="approve" @change="updateDecision()" /> 승인 | 
              <input type="radio" :name="'decision' + index" class="rejectRadio" v-model="item.decision" value="reject" @change="updateDecision()" /> 반려<br>
            </p>
            <p class="card-text d-flex align-items-center justify-content-between">
              <strong>반려사유: </strong>
              <input type="text" class="form-control rejectReason ml-2" v-model="item.rejectReason" style="flex: 1;" />
            </p>
          </div>
        </div>
      </div>

      <!-- 전체 선택 버튼과 저장 버튼 (모든 화면에서 보임) -->
      <div class="d-flex justify-content-end align-items-center mb-3">
        <button class="btn btn-primary ml-3" id="saveButton" @click="save">저장</button>
      </div>

      <!-- 페이징 -->
      <div class="d-flex justify-content-center">
        <ul class="pagination">
          <li class="page-item" @click.prevent="prevPage"><a class="page-link" href="#" id="prevPage">&lt;</a></li>
          <li class="page-item"><a class="page-link" href="#" id="currentPage">{{ currentPage }}</a></li>
          <li class="page-item" @click.prevent="nextPage"><a class="page-link" href="#" id="nextPage">&gt;</a></li>
        </ul>
      </div>

      <!-- 미리보기 모달 -->
      <div class="modal fade" id="previewModal" tabindex="-1" role="dialog" aria-labelledby="previewModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="previewModalLabel">영수증 미리보기</h5>
              <button type="button" class="close ms-auto" data-bs-dismiss="modal" aria-label="Close" style="font-size: 1.5rem; font-weight: bold;">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body" @click="closePreviewModal">
              <img :src="selectedReceipt" alt="영수증 미리보기" class="img-fluid" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import * as bootstrap from 'bootstrap'; // Bootstrap 모듈 가져오기

const currentPage = ref(1);
const rowsPerPage = ref(5);
const selectAllDecision = ref(''); // 전체 승인/반려를 선택하는 라디오 버튼의 v-model

const data = ref([
  { date: '2024-03-13', type: '사무용품', reason: '프린터 잉크 구매', amount: 20000, receiptName: '영수증 사진 이름-1', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', decision: null, rejectReason: '' },
  { date: '2024-02-31', type: '교통비', reason: '출장 교통', amount: 15000, receiptName: '영수증 사진 이름-1', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', decision: null, rejectReason: '' },
  { date: '2024-01-05', type: '식비', reason: '점심 식사', amount: 10000, receiptName: '영수증 사진 이름-1', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', decision: null, rejectReason: '' },
]);

const isMobile = ref(window.innerWidth <= 1100);
const selectedReceipt = ref(null);

const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * rowsPerPage.value;
  return data.value.slice(start, start + rowsPerPage.value);
});

const save = () => {
  console.log('Data saved:', data.value);
};

const toggleApproveAll = () => {
  if (selectAllDecision.value === 'approve') {
    data.value.forEach(item => item.decision = 'approve');
  } else {
    data.value.forEach(item => item.decision = null);
  }
};

const toggleRejectAll = () => {
  if (selectAllDecision.value === 'reject') {
    data.value.forEach(item => item.decision = 'reject');
  } else {
    data.value.forEach(item => item.decision = null);
  }
};

const updateDecision = () => {
  const allApproved = data.value.every(item => item.decision === 'approve');
  const allRejected = data.value.every(item => item.decision === 'reject');

  if (allApproved) {
    selectAllDecision.value = 'approve';
  } else if (allRejected) {
    selectAllDecision.value = 'reject';
  } else {
    selectAllDecision.value = '';
  }
};

const prevPage = () => {
  if (currentPage.value > 1) {
    currentPage.value--;
  }
};

const nextPage = () => {
  const totalPages = Math.ceil(data.value.length / rowsPerPage.value);
  if (currentPage.value < totalPages) {
    currentPage.value++;
  }
};

const previewReceipt = (receiptUrl) => {
  selectedReceipt.value = receiptUrl;
  const modal = new bootstrap.Modal(document.getElementById('previewModal'));
  modal.show();
};

const closePreviewModal = () => {
  const modal = bootstrap.Modal.getInstance(document.getElementById('previewModal'));
  modal.hide();
};

const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 1100;
};

onMounted(() => {
  window.addEventListener('resize', updateIsMobile);
});
</script>

