<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">영수증 등록</h2>
      <p class="content-sub-title">영수증 등록 페이지</p>

      <div class="d-flex justify-content-end align-items-center">
        <button class="btn btn-primary search-button" @click="openModal">영수증 등록</button>
      </div>

      <!-- 테이블 보기 (큰 화면에서만 보임) -->
      <table class="table mt-3" v-if="!isMobile">
        <thead>
          <tr>
            <th>발생일</th>
            <th>구분</th>
            <th>사유</th>
            <th>금액</th>
            <th>영수증 사진</th>
            <th>결제 상황</th>
            <th>반려 사유</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in paginatedData" :key="item.date">
            <td>{{ item.date }}</td>
            <td>{{ item.type }}</td>
            <td>{{ item.reason }}</td>
            <td>{{ item.amount }}</td>
            <!-- <td><a href="#" @click.prevent="previewReceipt(item.receipt)" class="text-primary">{{ item.receiptName }}</a></td> -->
            <td><img :src="item.receipt" alt="영수증 이미지" width="50" @click="previewReceipt(item.receipt)" style="cursor: pointer;" /></td>
            <td :class="getStatusClass(item.status)">{{ item.status }}</td>
            <td>{{ item.rejectionReason }}</td>
          </tr>
        </tbody>
      </table>

      <!-- 카드 레이아웃 보기 (작은 화면에서만 보임) -->
      <div class="card-layout" v-if="isMobile">
        <div class="card" v-for="item in paginatedData" :key="item.date">
          <div class="card-header">
            <p class="card-title">{{ item.date }}</p>
          </div>
          <div class="card-body">
            <p class="card-text"><strong>구분:</strong> {{ item.type }}</p>
            <p class="card-text"><strong>사유:</strong> {{ item.reason }}</p>
            <p class="card-text"><strong>금액:</strong> {{ item.amount }}</p>
            <p class="card-text">
              <strong>영수증 사진: </strong>
              <a href="#" @click.prevent="previewReceipt(item.receipt)" class="text-primary">{{ item.receiptName }}</a>
            </p>
            <p class="card-text">
              <strong>결제 상황:</strong> <span :class="getStatusClass(item.status)">{{ item.status }}</span>
            </p>
            <p class="card-text"><strong>반려 사유:</strong> {{ item.rejectionReason }}</p>
          </div>
        </div>
      </div>

      <!-- 페이지네이션 버튼 -->
      <nav>
        <ul class="pagination justify-content-center" id="pagination">
          <li class="page-item" :class="{ active: page === currentPage }" v-for="page in totalPages" :key="page">
            <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
          </li>
        </ul>
      </nav>
    </div>

    <!-- 모달 창 추가 -->
    <div class="modal fade" id="receiptModal" tabindex="-1" role="dialog" aria-labelledby="receiptModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="receiptModalLabel">영수증 등록</h5>
            <button type="button" class="close" aria-label="Close" @click="closeModal">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <form @submit.prevent="submitReceipt">
              <div class="form-group">
                <label for="receiptImage">영수증 사진</label><br>
                <div class="mt-2"> <!-- 새로 추가된 div로 파일 선택 버튼을 감쌉니다 -->
                  <input type="file" class="form-control-file" id="receiptImage" @change="handleFileChange" />
                </div>
              </div><br>
              <div class="form-group">
                <label for="date">발생일</label>
                <input type="date" class="form-control" id="date" v-model="formData.date" />
              </div><br>
              <div class="form-group">
                <label for="type">구분</label>
                <select class="form-control" id="type" v-model="formData.type">
                  <option>식비</option>
                  <option>교통비</option>
                </select>
              </div><br>
              <div class="form-group">
                <label for="amount">금액</label>
                <div class="input-group">
                  <input type="text" class="form-control" id="amount" placeholder="내용을 입력해주세요" v-model="formData.amount" />
                  <div class="input-group-append">
                    <span class="input-group-text">원</span>
                  </div>
                </div>
              </div><br>
              <div class="form-group">
                <label for="reason">사유</label>
                <input type="text" class="form-control" id="reason" placeholder="내용을 입력해주세요" v-model="formData.reason" />
              </div><br>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" @click="closeModal">취소</button>
                <button type="submit" class="btn btn-primary">등록</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>

    <!-- 미리보기 모달 -->
    <div class="modal fade" id="previewModal" tabindex="-1" role="dialog" aria-labelledby="previewModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="previewModalLabel">영수증 미리보기</h5>
            <button type="button" class="close ms-auto" data-bs-dismiss="modal" aria-label="Close" style="font-size: 1.5rem; font-weight: bold;">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body" @click="closePreviewModal">
            <img :src="selectedReceipt" alt="영수증 미리보기" class="img-fluid" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import * as bootstrap from 'bootstrap'; // Bootstrap JavaScript 모듈 가져오기

const data = ref([
  { date: '2024-08-01', type: '식비', reason: '야근', amount: '100,000원', receiptName: '깃허브 사진', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', status: '반려', rejectionReason: '해당 지원사항 X' },
  { date: '2024-08-02', type: '교통비', reason: '출장', amount: '50,000원', receiptName: '깃허브 사진', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', status: '승인', rejectionReason: '' },
  { date: '2024-08-03', type: '식비', reason: '야근', amount: '70,000원', receiptName: '깃허브 사진', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', status: '승인', rejectionReason: '' },
  { date: '2024-08-04', type: '교통비', reason: '출장', amount: '30,000원', receiptName: '깃허브 사진', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', status: '신청중', rejectionReason: '' },
  { date: '2024-08-05', type: '식비', reason: '야근', amount: '80,000원', receiptName: '깃허브 사진', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', status: '반려', rejectionReason: '해당 지원사항 X' },
  { date: '2024-08-06', type: '식비', reason: '야근', amount: '90,000원', receiptName: '깃허브 사진', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', status: '신청중', rejectionReason: '' },
  { date: '2024-08-07', type: '교통비', reason: '출장', amount: '40,000원', receiptName: '깃허브 사진', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', status: '승인', rejectionReason: '' },
  { date: '2024-08-08', type: '식비', reason: '야근', amount: '100,000원', receiptName: '깃허브 사진', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', status: '반려', rejectionReason: '해당 지원사항 X' },
  { date: '2024-08-09', type: '교통비', reason: '출장', amount: '55,000원', receiptName: '깃허브 사진', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', status: '승인', rejectionReason: '' },
  { date: '2024-08-10', type: '식비', reason: '야근', amount: '100,000원', receiptName: '깃허브 사진', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', status: '반려', rejectionReason: '해당 지원사항 X' },
  { date: '2024-08-11', type: '교통비', reason: '출장', amount: '65,000원', receiptName: '깃허브 사진', receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4', status: '승인', rejectionReason: '' }
]);

const currentPage = ref(1);
const itemsPerPage = ref(5);
const isMobile = ref(window.innerWidth <= 1150);
const formData = ref({
  receiptImage: null,
  date: '',
  type: '식비',
  amount: '',
  reason: ''
});

const selectedReceipt = ref(null); // 정의되지 않은 변수 해결

const totalPages = computed(() => Math.ceil(data.value.length / itemsPerPage.value));

const paginatedData = computed(() => {
  const startIndex = (currentPage.value - 1) * itemsPerPage.value;
  return data.value.slice(startIndex, startIndex + itemsPerPage.value);
});

const goToPage = (page) => {
  currentPage.value = page;
};

const openModal = () => {
  const modal = document.getElementById('receiptModal');
  const bootstrapModal = new bootstrap.Modal(modal);
  bootstrapModal.show();
};

const closeModal = () => {
  const modal = document.getElementById('receiptModal');
  const bootstrapModal = bootstrap.Modal.getInstance(modal);
  bootstrapModal.hide();
};

const handleFileChange = (event) => {
  formData.value.receiptImage = event.target.files[0];
};

const submitReceipt = () => {
  console.log('Receipt Submitted:', formData.value);
  closeModal();
};

const previewReceipt = (receiptUrl) => {
  selectedReceipt.value = receiptUrl;
  const modal = new bootstrap.Modal(document.getElementById('previewModal'));
  modal.show();
};

const closePreviewModal = () => {
  const modal = bootstrap.Modal.getInstance(document.getElementById('previewModal'));
  modal.hide();
};

const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 1150;
};

const getStatusClass = (status) => {
  if (status === '신청중') return 'status-pending';
  if (status === '승인') return 'status-approved';
  if (status === '반려') return 'status-rejected';
  return '';
};

onMounted(() => {
  window.addEventListener('resize', updateIsMobile);
});

</script>

