// src/router/index.js
import { createRouter, createWebHistory } from 'vue-router';
import receiptRoutes from './receipt';
import commonRoutes from './common';
import { isLoggedIn } from '@/utils/auth'; // 로그인 상태 확인 함수
import UserLogin from '@/views/common/UserLogin.vue';
import AdminLogin from '@/views/common/AdminLogin.vue';

const routes = [
  {
    path: '/',
    name: 'UserLogin',
    component: UserLogin,
    meta: { isLoginPage: true, requiresAuth: false }, // 로그인 페이지임을 명시
  },
  {
    path: '/admin/login',
    name: 'AdminLogin',
    component: AdminLogin,
    meta: { isLoginPage: true, requiresAuth: false }, // 로그인 페이지임을 명시
  },
  {
    path: '/receipt',
    name: 'Receipt',
    children: receiptRoutes,
  },
  {
    path: '/com',
    name: 'Common',
    children: commonRoutes,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

// 라우트 네비게이션 가드 설정
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // 인증이 필요한 경로인지 확인
    if (!isLoggedIn()) {
      // 로그인되어 있지 않다면 로그인 페이지로 리다이렉트
      next({ name: 'UserLogin' });
    } else {
      // 로그인되어 있다면 원래 경로로 이동
      next();
    }
  } else {
    // 인증이 필요하지 않은 경로는 그냥 이동
    next();
  }
});

export default router;
