<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">사용자 정보</h2>
      <form @submit.prevent="handleSubmit">
        <div class="form-group">
          <label for="userName">이름</label>
          <input type="text" class="form-control" id="userName" :value="userName" disabled>
        </div>
        <div class="form-group">
          <label for="department">부서</label>
          <input type="text" class="form-control" id="department" v-model="department" placeholder="부서를 입력하세요">
        </div>
        <div class="form-group">
          <label for="position">직급</label>
          <input type="text" class="form-control" id="position" v-model="position" placeholder="직급을 입력하세요">
        </div>
        <div class="form-group">
          <label for="location">파견 위치</label>
          <input type="text" class="form-control" id="location" v-model="location" placeholder="파견 위치를 입력하세요">
        </div>
        <div class="form-group">
          <label for="phone">핸드폰 번호</label>
          <div class="input-group">
            <input type="text" class="form-control" id="phone" v-model="phone" placeholder="핸드폰 번호를 입력하세요">
            <div class="input-group-append">
              <button class="btn btn-outline-secondary" type="button" @click="verifyPhone">인증</button>
            </div>
          </div>
        </div>
        <div class="form-group">
          <label for="verificationCode">인증 번호</label>
          <div class="input-group">
            <input type="text" class="form-control" id="verificationCode" v-model="verificationCode" placeholder="인증 번호를 입력하세요">
            <div class="input-group-append">
              <button class="btn btn-outline-secondary" type="button" @click="verifyCode">인증 확인</button>
            </div>
          </div>
        </div>
        <div class="form-group submit-button">
          <button type="submit" class="btn btn-primary">저장</button>
          <button type="button" class="btn btn-secondary" @click="handleCancel">취소</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import axios from 'axios';

// 데이터 바인딩을 위한 ref 설정
const userName = ref('권 범 준 (qjawns0618@kpcnc.co.kr)');
const department = ref('');
const position = ref('');
const location = ref('');
const phone = ref('');
const verificationCode = ref('');

// 핸드폰 인증 버튼 클릭 시 호출되는 함수
const verifyPhone = async () => {
  try {
    const response = await axios.post('/api/verifyPhone', { phone: phone.value });
    // 핸드폰 인증 처리 로직
    console.log(response.data);
  } catch (error) {
    console.error('Phone verification error:', error);
  }
};

// 인증 번호 확인 버튼 클릭 시 호출되는 함수
const verifyCode = async () => {
  try {
    const response = await axios.post('/api/verifyCode', { code: verificationCode.value });
    // 인증 번호 확인 처리 로직
    console.log(response.data);
  } catch (error) {
    console.error('Verification code error:', error);
  }
};

// 폼 제출 시 호출되는 함수
const handleSubmit = () => {
  // 폼 제출 처리 로직
  console.log('Form submitted');
};

// 취소 버튼 클릭 시 호출되는 함수
const handleCancel = () => {
  // 취소 처리 로직
  console.log('Action cancelled');
};
</script>

